* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background-color: #192734;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.center {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  display: flex;
  border: 2px solid wheat;
  width: 400px;
  height: 75%;
  flex-direction: column;
  justify-content: center;
}
/*------------------------------header starts */

.center-h {
  display: flex;
  width: 100%;
  height: 20%;
  background-color: #00b2ff;
  justify-content: center;
}

.header {
  display: flex;
  width: 95%;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  background-color: #00b2ff;
}
.bot-profile {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 3px solid green;
}
.bot-profile img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.Title p1 {
  font-family: monospace;
  color: black;
  font-size: 18px;
}
.Title p {
  color: green;
  font-size: 15px;
}

/* -------------------------header ends */

/* -------------messege body starts------------------ */

.center-mb {
  display: flex;
  width: 100%;
  min-height: 100%;
  background-color: white;
  justify-content: center;
  border: 2px solid greenyellow;
}
.msg-body {
  width: 95%;
  height: 100%;
  background-color: white;
}
.hmsg-container,
.bmsg-container {
  display: flex;
  margin: 20px 0px;
  width: 100%;
  height: auto;
  justify-content: center;
}
.pp {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid green;
}
.pp img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.msg {
  width: 100%;
  height: auto;
  padding: 12px;
  background-color: #10446e;
  border-radius: 10px;
  font-size: 17px;
}

/* ---------------messegae body ends---------------------- */

/* -----------------------input section starts--------------------------- */

.userinput {
  display: flex;
  width: 100%;
  height: 9%;
  background-color: white;
  flex-direction: row;
  align-items: center;
}
.box {
  display: flex;
  width: 100%;
  height: 100%;
}
.form-control {
  border: none;
  padding: 2px 10px;
  width: 320px;
  height: 100%;
  font-family: monospace;
}
.sent {
  display: flex;
  width: 20%;
  padding: 8px 0px;
  justify-content: center;
}
.far {
  color: #00b2ff;
}
